.el-progress {
  position: relative;
  line-height: 1;
}
.el-progress__text {
    font-size: 14px;
    color: #606266;
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px;
    line-height: 1;
}
.el-progress__text i {
      vertical-align: middle;
      display: block;
}
.el-progress--circle {
    display: inline-block;
}
.el-progress--circle .el-progress__text {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      text-align: center;
      margin: 0;
      -webkit-transform: translate(0, -50%);
              transform: translate(0, -50%);
}
.el-progress--circle .el-progress__text i {
        vertical-align: middle;
        display: inline-block;
}
.el-progress--without-text .el-progress__text {
    display: none;
}
.el-progress--without-text .el-progress-bar {
    padding-right: 0;
    margin-right: 0;
